<template>
	<div id="about">
		<div class="about">
			<img class="icon" :src="icon"/>
			<p>华建项目申报App 版本V1.1</p>
			<ul>
				<li class="cont_frame about_list" @click="showModel">
					<span>新版更新</span>
					<span class="new fr">
						<i class="new_hint">新版本</i>
						<span>V1.2</span>
					</span>
				</li>
			</ul>
		</div>
		<!-- 下载提示窗口 -->
		<PopModel :show="isShow" @close="hideModel">
			<div slot="content">
				<div class="title">是否更新到 V1.2 版本？</div>
				<p>1.我们杀了这个程序员祭天</p>
				<p>2.杀了那个产品摆酒</p>
				<p>3.优化了无数功能</p>
			</div>
			<div slot="footer">				
				<button class="button" style="width: 100%;" @click="download">安装</button>
			</div>
		</PopModel>
	</div>
</template>

<script>
	// 组件
	import PopModel from "components/Modal/PopModal.vue";
	// 通用js
	import Common from 'common/common.js';
	// Api方法
	import Api from "api/about.js";

	export default {
		name: "about",
		components: { PopModel },
		data(){
			return{
				// 图标
				icon: require('@/assets/images/icon_256x256.png'),
				// 显示弹窗
				isShow: false
			}
		},
		created(){
			this.init();
		},
		methods:{
			init(){
                this.$store.commit('SET_NAV_TITLE', '关于我们');
			},
			showModel(){
				this.isShow = true;
			},
			hideModel(){
				this.isShow = false;
			},
			// 下载新版本
			download(){
				this.hideModel();
			}
		}
	};
</script>

<style lang="less">
	// 引入通用设置文件
	@import "../../assets/less/setting";

	/* about_cont */

	.about{

		text-align: center;

		.icon{
			.wd(80);
			.ht(80);
			.mt(30);
			.mb(20);
		}

		ul{
			text-align: left;
			.mt(30);

			li{
				cursor: pointer;
			}
		}
		
		.new_hint{
			background: @warn_color;
			color:#fff;
			padding: 2px 5px;
			font-style: normal;

			.ft(12);
			.mr(5);
			.border_radius(4);
		}
	}

	.title{
		color: @base_color;
		font-weight: bold;

		.ft(16);
		.mb(15);
	}
</style>



