<template>
	<div id="message">
		<!-- 加载数据 -->
		<Loading v-if="pageLoading"></Loading>
		<!-- 加载结束 -->
		<div v-else>
			<ul class="inform_list">
				<li v-for="(item, index) in msgList" :key="index" class="cont_frame inform_list_item">
					<h3 class="inform_list_title">{{ item.title }}</h3>
					<MsgList :data="item.list" :num="item.list.length" :page-type="2"></MsgList>
				</li>
			</ul>
		</div>
		<!-- 操作栏 -->
		<div id="operateBar">
			<ul class="operate_bar">
				<li class="operate_item">
					<i class="operate_icon icon_readed" @click="readAll"></i>
					<span class="operate_title">全部已读</span>
				</li>
			</ul>
		</div>
	</div>
</template>

<script>
	// 组件
	import Loading from "components/Common/Loading.vue";
	import MsgList from "components/Message/MsgList.vue";
	// 通用js
	import Common from 'common/common.js';
	// Api方法
	import Api from "api/message.js";

	export default {
		name: "message",
		components: { Loading, MsgList },
		data(){
			return{
				// 是否加载
				pageLoading: false,
				// 消息列表
				msgList: [
					{
						title: '来自【南宁市发改委】最新发布的通知公告',
						list:[
							{
								readState: 0,
								id: 1964,
								keyWords: ["项目"],
								publishDate: "2018-10-10",
								title: "(1) 关于“发改书吧”信息共享平台等三个项目信息安全等级测评及第三方测评服务竞争性谈判延期开标的公告"
							},
							{
								readState: 0,
								id: 1965,
								keyWords: ["项目"],
								publishDate: "2018-10-10",
								title: "(2) 东盟新型智慧城市生态共赢圈项目前期设计服务竞争性磋商公告"
							},
						]
					},
					{
						title: '来自【南宁人社厅】最新发布的通知公告',
						list:[
							{
								readState: 0,
								id: 1966,
								keyWords: ["培训"],
								publishDate: "2018-10-10",
								title: "(1) 广西壮族自治区人力资源和社会保障厅关于举办广西壮族自治区第一届创业培训师资大赛的通知"
							}
						]
					}
				]
			}
		},
		created(){
			this.init();
		},
		methods:{
			init(){
                this.$store.commit('SET_NAV_TITLE', '消息中心');
			},
			// 全部已读
			readAll(){
				this.msgList.map(item => {
					item.list.map(i =>{
						i.readState = 1;
					})
				})

			}
		}
	};
</script>

<style lang="less" scoped>
	// 引入通用设置文件
	@import "../../assets/less/setting";
	@import "../../assets/less/news_list";
	@import "../../assets/less/operate_bar";

	.operate_item {
		width: 100%;
    }

	.inform_list_title{
		color:@base_color;
		display: inline-block;
	}
</style>



